import { useEffect, useState } from "react";

const AGENTS = [
  { id: "A", url: "http://localhost:3001" },
  { id: "B", url: "http://localhost:3002" },
];

export default function Home() {
  const [data, setData] = useState({});
  const [payload, setPayload] = useState("Hello from UI");
  useEffect(() => {
    async function load() {
      const d = {};
      for (const a of AGENTS) {
        try {
          const logs = await fetch(a.url + "/logs").then(r => r.json());
          const intents = await fetch(a.url + "/intents").then(r => r.json());
          d[a.id] = { logs, intents };
        } catch (e) {
          d[a.id] = { error: e.message };
        }
      }
      setData(d);
    }
    load();
    const id = setInterval(load, 3000);
    return () => clearInterval(id);
  }, []);

  async function send(fromUrl, toUrl) {
    await fetch(fromUrl + "/send-request", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ receiverUrl: toUrl, payload: { text: payload } })
    }).then(r => r.json()).then(console.log).catch(console.error);
  }

  return (
    <div style={{ padding: 24 }}>
      <h1>Agent Dashboard (MVP)</h1>
      <div style={{ marginBottom: 16 }}>
        <input value={payload} onChange={e => setPayload(e.target.value)} style={{ width: 400 }} />
      </div>
      <div style={{ display: "flex", gap: 24 }}>
        {AGENTS.map(a => (
          <div key={a.id} style={{ border: "1px solid #ddd", padding: 12, width: 420 }}>
            <h3>Agent {a.id}</h3>
            <button onClick={() => send(a.url, a.id === "A" ? "http://localhost:3002" : "http://localhost:3001")}>
              Send to {a.id === "A" ? "B" : "A"}
            </button>
            <h4>Intents</h4>
            <pre style={{ maxHeight: 200, overflow: "auto" }}>
              {JSON.stringify(data[a.id]?.intents?.slice(0, 10) || data[a.id]?.error || "loading", null, 2)}
            </pre>
            <h4>Logs</h4>
            <pre style={{ maxHeight: 200, overflow: "auto" }}>
              {JSON.stringify(data[a.id]?.logs?.slice(0, 10) || [], null, 2)}
            </pre>
          </div>
        ))}
      </div>
    </div>
  )
}
